import Vue from 'vue'
import Vuex from 'vuex'
import productService from './productService.js'
import _ from 'lodash'

Vue.use(Vuex)

export default new Vuex.Store({
	state:{
		cartItems: []
	},
	getters:{
		getCartItems: state => state.cartItems
	},
	actions:{
		loadCartItems(context, cartItems){
			context.commit('loadCartItems', cartItems);
		},
		addCartItem(context){
			let product = productService.product;
			let items = this.state.cartItems;


			let id=0;
			if(items.length>0)
				id=items[items.length-1].id;
			++id;

			context.commit('addCartItem', {
					id:id,
					product:product[0],
					count:1,
					totalPrice:product[0].price250,
					price:product[0].price250,
					size:250,
					type: 'Молотый'
			});
		},
		removeCartItem(context, id){
			context.commit('removeCartItem',id)
		}

	},
	mutations:{
		loadCartItems(state, payLoad){
			state.cartItems=payLoad;
		},
		addCartItem(state, item){
			state.cartItems.push(item)
		},
		removeCartItem(state, id){
			console.log(id)
			var index = state.cartItems.map(x => {
			  return x.id;
			}).indexOf(id);

			state.cartItems.splice(index, 1);
		}
	}
})