<template>
	<div class="number-input">
		<button @click="decreaseValue" class="control-btn">
			<font-awesome-icon icon="minus" />
		</button>
		<span class="number-input__value">
			<!-- <input type="text" :value="localValue" @change="changeValue" readonly class="number-input__input"> уп. -->
		 {{localValue}} уп.
		</span>
		<button @click="increaseValue" class="control-btn">
			<font-awesome-icon icon="plus" />
		</button>
	</div>
</template>

<script>
	import Vue from 'vue'

	export default {
		props:{
	    	value:{
		      type:Number,
		      required:true,
		      default: 1
      }
    },
    computed:{
    	localValue:{
    		get: function() {
        	return this.value
        },
        set: function(value) {
        	this.$emit('changeValue', value)
        }
    	}
    },
    methods:{
    	increaseValue(){
    		++this.localValue;
    	},
    	decreaseValue(){
    		if ((this.localValue-1)>0)
    			--this.localValue
    	}
    }
	}
</script>

<style lang="scss">
	.number-input{
		display: flex;

		&__value{
			display: inline-flex;
			flex-shrink: 1;
			flex-grow: 1;
    	align-items: center;
    	justify-content: center;
			background-color: $lighter;
			border-radius: 20px;
			height: 40px;
			padding: 0 10px;
			overflow: hidden;
			margin:0 5px;
			white-space: nowrap;
		}
	}
</style>