<template>
  <section class="section cart-section">
    <!-- <div class="container"> -->
    <div class="top-img section__top-img" :style="'background-image: url('+require('../assets/form-bg.jpg')+')'"> 
      <img :src="require('../assets/logo.png')" alt="product Factory" class="top-img__logo">
    </div>
    <div class="container">
      <header class="section__header header">
        <h2>Форма заказа</h2>
      </header>
      <div class="cart-section__text">
        Пожалуйста, заполните форму ниже или позвоните нам <a href="tel:+375296491025" class="phone-link">+375 (29) 649-10-25</a> и мы поможем с выбором:
      </div>
      <!-- </div> -->
      <div class="cart__sale-banner">
        <div class="sale-banner">
          <img :src="require('../assets/sale-red.png')" alt="Скидка" class="sale-banner__img">
          <span>При заказе от 5кг скидка 10%</span>
        </div>
      </div>
      <!-- <div class="cart__items">
        <div>
          <cart-item v-for="(item, i) in cartItems" :cartItem="item" @removeItem="removeItem(item.id)" @setPrice="function(price){item.price=price}" class="cart__item"></cart-item>
        </div>
        <div class="cart__add-item">
          <button class="btn" @click="addItem()"><font-awesome-icon icon="plus" class="btn__icon"/> Добавить наименование</button>
        </div>
      </div> -->

      
        <transition :name="transitionName" mode="out-in">
          <keep-alive>
                <router-view class="cart__view"></router-view>
              </keep-alive>
            </transition>
        
      <div class="cart__total cart-total">
        <header class="header">
          <h3>Скидки и сумма к оплате</h3>
        </header>
        <div class="row">
          <div class="col-12 col-sm-4 cart-total__item">
            Общий вес заказанного кофе:<br>
            <strong class="font_big">{{totalWeight}}</strong> кг.
          </div>
          <div class="col-12 col-sm-4 cart-total__item">
            Общая сумма заказа:<br>
            <strong class="font_big">{{totalPrice}}</strong> руб.
          </div>
          <div class="col-12 col-sm-4 cart-total__item">
            Общая сумма заказа c учетом скидки:<br>
            <strong class="font_big">{{totalSalePrice}}</strong> руб.
          </div>
        </div>
      </div>
      <div class="cart__submit">
        <router-link v-if="this.$router.currentRoute.name==='home'" :to="{name:'cart-form'}" class="btn btn_big btn_accent">
          Оформить заказ
        </router-link>
        <button v-else-if="this.$router.currentRoute.name==='cart-form'" class="btn btn_accent btn_big">Заказать</button>
      </div>
      <!-- <div >
        <div class="cart__submit">
          <router-link  :to="{name:'cart-form'}" class="btn btn_big btn_accent">
            Оформить заказ
          </router-link>
        </div>
      </div>
      <div v-else-if="this.$router.currentRoute.name==='cart-form'">
        <router-link :to="{name:'home'}" class="btn"><font-awesome-icon icon="arrow-left" class="btn__icon"/> Назад</router-link>
        <div class="cart__submit">
          
        </div>
      </div> -->
    </div>
  </section>
</template>
<script>
  import productService from '../productService.js'
  import _ from 'lodash'
  import {mapGetters} from 'vuex'

  export default {
    components:{
    },
    data(){
      return{
        product:[],
        transitionName:'slide-right'
        //cartItems:[]
      }
    },
    methods:{
      /*addItem(){
        this.cartItems.push({
          product:this.product[0],
          count:1,
          totalPrice:this.product[0].price
        })
      }*/
    },
    created(){
      this.product=productService.product;
    },
    watch: {
      '$route' (to, from) {
        let transitionName;
        if(to.name==='home' || from.name==='product-details'){
          transitionName='slide-left';
        } else {
          transitionName='slide-right';
        }

        this.transitionName = transitionName;
      }
    },
    computed:{
      /*cartItems(){
        //return this.$store.state.cartItems;
        return this.$store.getters.getCartItems
      },*/
      ...mapGetters({
        cartItems:'getCartItems'
      }),
      totalPrice:function(){
        let totalPrice=_.sumBy(this.cartItems,function(o){return o.price});
        return totalPrice;
      },
      totalWeight:function(){
        let totalWeight=_.sumBy(this.cartItems,function(o){return o.count*o.size})/1000;
        return totalWeight;
      },
      totalSalePrice(){
        let totalSalePrice=(this.totalWeight<5)?this.totalPrice:this.totalPrice-this.totalPrice*0.01;
        return totalSalePrice;
      }
    }
  }
</script>
<style lang="scss">
  .cart-section{

    &__text{
      text-align: center;
      margin: 20px auto ;
      max-width: 600px;
    }
  }

  .cart{
    &__view{
      margin-bottom: 40px
    }
    &__sale-banner{
      display: flex;
      justify-content: center;
      margin-bottom: 40px;
    }
    &__total{
      margin-bottom: 40px;
    }
    &__submit{
      text-align: center;
    }
  }
  .cart-total{
    &__item{
      margin-bottom: 20px;
    }
  }
  .sale-banner{
    display: flex;
    align-items: center;
    justify-content: center;
    border:1px solid $accent;
    padding: 0 20px;
    height: 50px;
    border-radius: 25px;
    text-transform: uppercase;
    color:$accent;

    &__img{
      margin-right: 10px;
    }
  }
  .slide-right-enter-active {
    transition: all .1s ease;
  }
  .slide-right-leave-active {
    transition: all .4s ease;
  }
  .slide-right-enter {
    transform: translateX(50px);
    opacity: 0;
  }
  .slide-right-leave-to{
    transform: translateX(-50px);
      opacity: 0;
  }

  .slide-left-enter-active {
    transition: all .1s ease;
  }
  .slide-left-leave-active {
    transition: all .4s ease;
  }
  .slide-left-enter {
    transform: translateX(-50px);
    opacity: 0;
  }
  .slide-left-leave-to {
    transform: translateX(50px);
      opacity: 0;
  }

  @include media-breakpoint-up(lg) {
    
  }

  @include media-breakpoint-up(md) {
    
  }

  @include media-breakpoint-up(sm) {
    .cart-total{
      &__item{
        margin-bottom: 0;
      }
    }
  }

  @include media-breakpoint-up(xs) {

  }
</style>