<template>
  <div class="cart-item clearfix row">
    <button class="cart-item__remove control-btn" @click="removeItem()">
      <font-awesome-icon icon="times" />
    </button>
    <div class="col-12 col-lg-3">
      <div class="img-responsive cart-item__img">
        <router-link :to="{name:'product-details', params:{id:cartItem.product.id}}">
          <img :src="cartItem.product.img" alt="">
        </router-link>
      </div>
      <router-link :to="{name:'product-details', params:{id:cartItem.product.id}}" class="cart-item__details">
        Подробнее
      </router-link>
    </div>


    <div class="col-12 col-md-4 col-lg-3">
      <div class="cart-item__field">
        <select v-model="cartItem.product" class="select-input">
          <option v-for="item in product" :value="item" :key="item.id">{{item.name}}</option>
        </select>
        <!-- <select-input :values="product" :valKey="'name'" :selectedValue="cartItem.product" @changeValue="function(value){cartItem.product=value}"></select-input> -->
      </div>
      <div class="cart-item__field">
        <select v-model="cartItem.type" class="select-input">
          <option value="Молотый">Молотый</option>
          <option value="В зёрнах">В зёрнах</option>
        </select>
      </div>
    </div>

    <div class="col-12 col-md-4 col-lg-3">
      <div class="cart-item__field">
        <select v-model="cartItem.size" class="select-input">
          <option value="250">250 г.</option>
          <option value="1000">1 кг.</option>
        </select>
      </div>
      <div class="cart-item__field">
        <number-input :value="cartItem.count" @changeValue="changeCount"></number-input>
      </div>
    </div>

    <div class="col-12 col-md-4 col-lg-3">
      <div class="cart-item__field">
        Стоимость:<br>
        <strong v-if="cartItem.size==250" class="font_big">{{cartItem.product.price250}}</strong>
        <strong v-else-if="cartItem.size==1000" class="font_big">{{cartItem.product.price1000}}</strong>
        руб.
      </div>
      <div class="cart-item__field">
        Общая стоимость:<br>
        <strong class="font_big">{{totalPrice}}</strong> руб.
      </div>
    </div>
        
          


  </div>
</template>

<script>
  import productService from '../productService.js'
  import NumberInput from './NumberInput.vue'
  //import SelectInput from './SelectInput.vue'


  export default {
    props:{
      cartItem:{
        type:Object,
        required:true
        //default: functin(){return{}}
      }
    },
    components:{
      NumberInput/*,SelectInput*/
    },
    data(){
      return{
        product:[]
      }
    },
    computed:{
      totalPrice:function(){
        let price;
        if(this.cartItem.size==250)
          price = this.cartItem.product.price250;
        else if(this.cartItem.size==1000)
          price = this.cartItem.product.price1000;

        price*=this.cartItem.count;
        this.$emit('setPrice', price);
        return price;
      }
    },
    methods:{
      viewproductDetails(){
        this.$router.push('/detail/'+this.cartItem.product.id)
      },
      removeItem(){
        this.$emit('removeItem')
      },
      changeCount(value){
        this.cartItem.count=value;
      }

    },
  created(){
      this.product=productService.product;
    }
  }
</script>

<style lang="scss">
  .cart-item{
    position: relative;
    
    &__remove{
      position: absolute;
      top:0;
      left:15px;
      z-index: 10;
    }
    &__img{
      max-width: 180px;
      border-radius: 50%;
      overflow:hidden;
      background-color: $lighter;
      border: solid 1px $light;
      position: relative;
      transition:border-color 0.3s ease;
      margin: 0 auto;
      /*&:hover{
        &:after{
          display: block;
          content: '';
          position: absolute;
          width: 100%;
          height: 100%;
          top:0;
          left:0;
          background-color: rgba(0,0,0,0.5);
        }
      }*/
    }
    &__details{
      //opacity: 0;
      display: block;
      text-align: center;
      
      margin: 10px auto;
      color:$accent;
      transition:opacity 0.3s ease;

      &:hover{
        color:$accent;
      }
    }
    &__field{
      margin-bottom: 20px;
    }
    &:hover{
      .cart-item__details{
        opacity: 1;
        transition:opacity 0.3s ease;
      }
      .cart-item__img{
        transition:border-color 0.3s ease;
        border-color:$accent;
      }
    }
  }
  
  @include media-breakpoint-up(lg) {
    
  }

  @include media-breakpoint-up(md) {
    .cart-item{
      &__details{
        opacity: 0;
      }
    }
    
  }

  @include media-breakpoint-up(sm) {

  }

  @include media-breakpoint-up(xs) {

  }


</style>