<template>
	<div class="cart-items">
		<transition-group name="list" tag="div">
			<cart-item v-for="(item, i) in cartItems" :cartItem="item" @removeItem="removeItem(item.id)" @setPrice="function(price){item.price=price}" v-bind:key="item.id" class="list-item cart-items__item"></cart-item>
		</transition-group>
		<div class="cart-items__add-item">
			<button class="btn" @click="addItem()"><font-awesome-icon icon="plus" class="btn__icon"/> Добавить наименование</button>
		</div>
	</div>
</template>
<script>
	import Vue from 'vue'
	import cartItem from '../components/CartItem.vue'
	import productService from '../productService.js'
	import _ from 'lodash'
	import {mapGetters, mapActions} from 'vuex'

	export default {
		components:{
			cartItem
		},
		data(){
			return{
				product:[],
				//cartItems:[]
			}
		},
		methods:{
			/*addItem(){
				this.cartItems.push({
					product:this.product[0],
					count:1,
					totalPrice:this.product[0].price
				})
			}*/
			...mapActions({
				addItem:'addCartItem',
				removeItem:'removeCartItem',
				loadCartItems:'loadCartItems'
			}),
		},
		created(){
			this.product=productService.product;
			this.addItem()
		},
		computed:{
			/*cartItems(){
				//return this.$store.state.cartItems;
				return this.$store.getters.getCartItems
			},*/
			...mapGetters({
				cartItems:'getCartItems'
			}),
			totalPrice:function(){
				let totalPrice=_.sumBy(this.cartItems,function(o){return o.price});
				return totalPrice;
			},
			totalWeight:function(){
				let totalWeight=_.sumBy(this.cartItems,function(o){return o.count*o.size})/1000;
				return totalWeight;
			},
			totalSalePrice(){
				let totalSalePrice=(this.totalWeight<5)?this.totalPrice:this.totalPrice-this.totalPrice*0.01;
				
					
				return totalSalePrice;
			}
		}
	}
</script>
<style lang="scss">
	.cart-items{
		&__item{
			&:not(:last-child){
				margin-bottom: 10px;
			}
		}
		&__add-item{
			margin-top: 40px;
		}
	}
	.list-enter-active, .list-leave-active {
	  transition: all 0.5s;
	}
	.list-enter, .list-leave-to {
	  opacity: 0;
	  transform: translateY(30px);
	}
</style>