import Vue from 'vue'
import Router from 'vue-router'
import CartItems from './views/CartItems.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/coffeefactory/',
      name: 'home',
      component: CartItems
    },
    {
      path: 'coffeefactory/detail/:id(\\d+)',
      name: 'product-details',
      component: () => import('./views/ProductDetails.vue')
    },
    {
      path: 'coffeefactory/submit',
      name: 'cart-form',
      component: () => import('./views/CartForm.vue')
    }
  ],
  mode:'history'
})
